
.contact{
  background:linear-gradient(to right,rgb(47, 85, 255),brown);
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  *{
    margin: 5px;
  }
  .div{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  }
  h3{
    margin-left: 23%;
    color: blue;
  }
  #cont{
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:column ;
    background-color: white;
    max-width:38vh;
    min-width: auto;
    border-radius: 10px;
    margin-bottom: 40px;
    #click{
background-color: blue;
color: white;
width: 95%;
    }
    input{
height: 30px;
width:35vh ;
border: hidden;
background-color: wheat;
border-radius: 3px;

    }
    textarea{
     width: 35vh;
      height:80px;
      background-color: wheat;
      border: hidden;
      border-radius: 3px;
    }
    p{
      color: blue;
    }
  }
  div{
    color:blue;
  }
  img{
    width: 40px;
    height: 20px;
    margin-bottom: -5px;
    margin-left: 0;
    
    
  }
  
 #im{
  width: 60px;
  height: 40px;
  margin-bottom: -10px;
  margin-left: -10px;
 }
  .goup{
    img{
      margin-top: 40px;
      height: 40px;
      width: 40px;
      animation-name:anim;
animation-duration:2s;
animation-iteration-count: infinite;
//animation-play-state: paused;
animation-delay: 1s;
    }

   
  }
  @keyframes anim{
    0%{transform: translateX(5px);}
    25%{transform: translateY(5px);}
    50%{transform: translateX(5px);}
    75%{transform: translateY(5px);}
    100%{transform: translateX(5px);}
    }
}


